import axios from "axios";

//const urlBase="http://localhost:8087/api/plant";
class PlantService{

    createPlant(plant){
        return axios.post("/api/plant/create",plant);
    }

    findAllDto(){
        return axios.get("/api/plant/findAllDto");
    }

    findAll(){
        return axios.get("/api/plant/findAll");
    }

    findById(id){
        return axios.get("/api/plant/findById/"+id);
    }

    updatePlant(id,plant){
        return axios.put("/api/plant/update/"+id,plant);
    }

    deletePlant(id){
        return axios.delete("/api/plant/delete/"+id);
    }
}
export default new PlantService()