import axios from "axios";

//const urlBase="http://localhost:8087/api/project";
class ProjectService{

    createProject(project){
        return axios.post("/api/project/create",project);
    }

    findAll(){
        return axios.get("/api/project/findAll");
    }

    findById(id){
        return axios.get("/api/project/findById/"+id);
    }

    updateProject(id,project){
        return axios.put("/api/project/update/"+id,project);
    }

    deleteProject(id){
        return axios.delete("/api/project/delete/"+id);
    }
    
}
export default new ProjectService()