import axios from "axios";

//const urlBase="http://localhost:8087/api/farmer";

class FarmerService {

    createFarmer(farmer){
        return axios.post("/api/farmer/create", farmer);
    }

    findAllDto(){
        return axios.get("/api/farmer/findAllDto");
    }
    findAll(){
        return axios.get("/api/farmer/findAll");
    }

    findById(id){
        return axios.get("/api/farmer/findById/"+id);
    }

    updateFarmer(id,farmer){
        return axios.put("/api/farmer/update/"+id,farmer);
    }

    deleteFarmer(id){
        return axios.delete("/api/farmer/delete/"+id);
    }
}
export default new FarmerService()